.container {
  width: 100%;
  margin-top: 50px;

  &__buttons {
    margin-top: 10px;

    & > button {
      padding: 10px;
      border-radius: 10px;
      background-color: #2e353b;
      color: white;
      outline: 0;
      border: 0;
      transition: 0.3s;
      cursor: pointer;

      &:hover {
        background-color: #222222;
      }
    }
  }

  .wheel {
    position: relative;

    &_disabled {
      opacity: 0.7;
      pointer-events: none;
    }

    & button {
      z-index: 2;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      cursor: pointer;
      width: 100px;
      color: white;
      aspect-ratio: 1;
      background-color: black;
      border: 3px solid white;
      border-radius: 50%;
      user-select: none;
    }

    & canvas {
      transition: 1s;
    }

    & svg {
      position: absolute;
      top: 50%;
      height: 75px;
      transform: translateY(-50%);
      z-index: 1;
      left: calc(50% + 31px);
    }

    &_active {
      & button {
        pointer-events: none;
      }
    }
  }

  .winner {
    display: flex;
    width: 100%;
    justify-content: center;
    align-items: center;
    color: white;
    user-select: none;

    &__container {
      position: relative;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 24px;
      background: black;
      width: 500px;
      text-align: center;
      aspect-ratio: 8/2;
      border-radius: 10px;
      border: 2px solid black;
      box-shadow: black 0 0 20px;
      pointer-events: none;

      &_active {
        pointer-events: all;
      }
    }

    &__image {
      border-radius: 10px 0 0 10px;
      height: 100%;
      width: 100%;
      opacity: 0;
      background-repeat: no-repeat;
      background-position: center;
      background-size: 100% auto;
      transition: 0.3s;
      max-width: 0;

      &_active {
        opacity: 1;
        max-width: 130px;
      }
    }

    &__content {
      width: 100%;
      height: 100%;
      display: flex;
      gap: 10px;
    }

    &__link {
      font-size: 18px;
      text-align: center;
      color: white;
    }

    &__text {
      display: grid;
      width: 100%;
      padding: 10px;
    }

    &__platform {
      font-size: 14px;
    }
  }
}

.img {
  position: absolute;
  left: 0;
  width: 96px;
  height: 96px;
  border-radius: 30px;
}
